import React, { Fragment, useEffect, useState, useRef } from 'react'
import {
    Menu, X, ChevronDown, Share, Printer, FileText, File, Grid, Copy, Plus, MoreVertical, Edit, Archive, Trash, Search, Eye, Lock, ChevronLeft, RefreshCw
} from 'react-feather'
import {
    Button, Input, Label, Row, Col, Form, FormGroup, Spinner, Nav, InputGroup, InputGroupAddon, InputGroupText
} from 'reactstrap'
import Select from 'react-select'
import { selectThemeColors, transformInToFormObject } from '@utils'
import useJwt from '@src/auth/jwt/useJwt'
import { toast } from 'react-toastify'
import { Link, useHistory } from 'react-router-dom'
import { PHONE_PREFIX } from '../../Configuarables'
import { Error, Success, ErrorMessage } from '../../viewhelper'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import search from '../../../assets/images/icons/search-icon.svg'
import infinity from '../../../assets/images/logo/infiniti_logo.svg'
const MySwal = withReactContent(Swal)
import './EstateAdminReg.scss'

const EstateAdminReg = () => {
    const history = useHistory()
    const addressRef = useRef()
    const [burger, setBurger] = useState(false)
    const [isLoading, setLoading] = useState(false)
    const [postcode, setPostCode] = useState('')
    const [searching, setSearching] = useState(false)
    const [ukaddress, setukaddress] = useState([])
    const [userInput, setUserInput] = useState({
        mobile: '',
        email: '',
        full_name: '',
        address: ''
    })
    const [error, seterror] = useState({
        fullName: false,
        email: false,
        mobile: false,
        mobileZero: false,
        postcode: false,
        address: false
    })
    const handlePostCodeSearch = e => {
        e.preventDefault()
        setSearching(true)
        setUserInput({ ...userInput, address: '' })
        useJwt.ukAddressEndpoint({ postCode: postcode }).then(res => {
            console.log(res.data.payload)
            setukaddress(res.data.payload.addresses.map(element => `${element.building_number}, ${element.thoroughfare}, ${element.locality}, ${element.town_or_city}`))
            setSearching(false)
            seterror({address: true, postcode: false})
        }).catch(err => {
            setukaddress([])
            setSearching(false)
            Error(err)
            seterror({...error, postcode: true})
        })
    }
   
    const handleChange = e => {
        const chkEmail = /\S+@\S+\.\S+/.test(e.target.value)
        // Email Validator
        if (e.target.name === 'email' && chkEmail) {
            seterror({ ...error, email: false })
        }
        if (e.target.name === 'email' && !chkEmail) {
            seterror({ ...error, email: true })
        }
        //full name validator
        if (e.target.name === "full_name") {
            if (e.target.value.length < 0 || e.target.value.length > 32) {
                seterror({ ...error, fullName: true })
            } else {
                seterror({ ...error, fullName: false })
            }
        }
        //mobile number validator
        if (e.target.name === "mobile") {
            // console.log(e.target.value)
            if (e.target.value[0] === '0') {
                seterror({ ...error, mobileZero: true })
                return 0
            } else if (e.target.value.length < 10) {
                seterror({ mobile: true })
                // seterror({ mobileZero: false })
            } else {
                seterror({ ...error, mobile: false })
            }
            if (!/^[0-9]*$/.test(e.target.value)) {
                return 0
            }
        }
        setUserInput({ ...userInput, [e.target.name]: e.target.value })
    }
    const handleSubmit = e => {
        e.preventDefault()
        console.log(userInput)
        const { mobile, email, full_name, address } = userInput
        // const chkEmail = /\S+@\S+\.\S+/.test(email)
        if (error.fullName || error.email || error.mobile || !userInput.address || error.postcode) {
            return 0
        }
        setLoading(true)
        useJwt.estateAdminReg({ mobile, email, full_name, address }).then(res => {
            console.log(res)
            setLoading(false)
            history.push('/login')
            // MySwal.fire({
            //     title: 'Done!',
            //     // text: 'You clicked the button!',
            //     icon: 'success',
            //     customClass: {
            //         confirmButton: 'btn btn-primary'
            //     },
            //     buttonsStyling: false
            // })
            // setPostCode('')
            // setUserInput({
            //     mobile: '',
            //     email: '',
            //     full_name: '',
            //     address: ''
            // })

        }).catch(err => {
            Error(err)
            setLoading(false)
        })
    }

    const burgerHandlr = () => {
        setBurger(!burger)
    }
    return (
        <main style={{ position: 'relative' }}>
            <header style={{ backgroundColor: 'white' }}>
                <div className='custom-nav'>
                    <a href="https://infiniti-property.com/"><img src={infinity} alt="" /></a>
                    {/* collapsable menue */}
                    <div className='menu-icon'>
                        <Menu size='38px' onClick={burgerHandlr} style={{ color: '#999999' }}></Menu>
                        <div className={burger ? 'collaps-nav' : 'collapsable'}>
                            <div style={{ textAlign: 'right', marginBottom: '20px' }}>
                                <X onClick={burgerHandlr} size='45px'></X>
                            </div>
                            <div className='collaps-nav-items'>
                                <a href='https://infiniti-property.com/' className='nav-item-collaps'>Home</a>
                                <a href='https://infiniti-property.com/#whyinfiniti' className='nav-item-collaps'>Why i-Prop</a>
                                <a href='https://infiniti-property.com/#features' className='nav-item-collaps'>Features</a>
                                <a href='https://infiniti-property.com/#contact' className='nav-item-collaps'>Contact Us</a>
                                <a href='http://18.134.192.108:9003/EstateAdminRegistration' className='nav-item-collaps'>Register</a>
                            </div>
                        </div>
                    </div>
                    {/* main menu */}
                    <div className='collapsable'>
                        <div>
                            <a href='https://infiniti-property.com/' className='nav-item'>Home</a>
                            <a href='https://infiniti-property.com/#whyinfiniti' className='nav-item'>Why i-Prop</a>
                            <a href='https://infiniti-property.com/#features' className='nav-item'>Features</a>
                            <a href='https://infiniti-property.com/#contact' className='nav-item'>Contact Us</a>
                            <a href='http://18.134.192.108:9003/EstateAdminRegistration' className='nav-item'>Register</a>
                        </div>
                    </div>
                </div>
            </header>
            <div style={{ width: '100%', minHeight: '100vh' }} className='reg-wrapper'>
                <Form className="row pt-1 m-auto eAdminRegForm" onSubmit={handleSubmit} autoComplete="off">
                    <p className="text-center" style={{ margin: '20px auto 30px', fontFamily: 'Fira Sans', color: '#6E6B7B', fontWeight: '100px', fontSize: '1.5rem' }}>
                        Register as an Estate-Admin
                    </p>
                    <Col md='12' >
                        <FormGroup>
                            <Label for="full_name">Full Name <span style={{ color: '#B94A48' }}>*</span></Label>
                            <Input
                                required
                                type="text"
                                id='full_name'
                                name="full_name"
                                placeholder="Your name"
                                value={userInput.full_name}
                                onChange={handleChange}
                            />
                            {error.fullName &&  <Label style={{ color: '#B94A48', fontSize: '10px' }}>First Name must be between 1 and 32 characters!</Label>}
                        </FormGroup>
                    </Col>
                    <Col md='12' >
                        <FormGroup>
                            <Label className='form-label' for='mobile'>
                                Mobile Number  <span style={{ color: '#B94A48' }}>*</span>
                            </Label>
                            <InputGroup className='input-group-merge w-100'>
                                <InputGroupAddon addonType='prepend'>
                                    <InputGroupText>
                                        {PHONE_PREFIX}
                                    </InputGroupText>
                                </InputGroupAddon>
                                <Input
                                    autoFocus
                                    required
                                    type='text'
                                    minLength={10}
                                    maxLength={10}
                                    id='mobile'
                                    name='mobile'
                                    value={userInput.mobile}
                                    onChange={handleChange}
                                    placeholder='123-456-7899'
                                />
                            </InputGroup>
                            {error.mobile && <Label style={{ color: '#B94A48', fontSize: '10px' }}>Phone number must be more than 10 digit!</Label>}
                            {error.mobileZero && <Label style={{ color: '#B94A48', fontSize: '10px' }}>0 is not valid at first position!</Label>}
                        </FormGroup>
                    </Col>
                    <Col md='12' >
                        <FormGroup>
                            <Label for="email">Email  <span style={{ color: '#B94A48' }}>*</span></Label>
                            <Input
                                required
                                type="email"
                                id='email'
                                name="email"
                                placeholder="example@gmail.com"
                                value={userInput.email}
                                onChange={handleChange}
                            />
                            {error.email && userInput.email && <Label style={{ color: '#B94A48', fontSize: '10px' }}>E-Mail address does not appear to be valid!</Label>}
                        </FormGroup>
                    </Col>

                    <Col md='12' >
                        <Label for="postcode">Search Address by Post Code  <span style={{ color: '#B94A48' }}>*</span></Label>
                        <InputGroup>
                            <Input
                                // required
                                type="text"
                                id='postcode'
                                name="postcode"
                                placeholder="enter a valid postcode"
                                value={postcode}
                                onChange={e => setPostCode(e.target.value)}
                                style={{ borderRight: 'none' }}
                            />
                            <InputGroupAddon addonType='append'>
                                <button className='search-btn' type='button' onClick={handlePostCodeSearch}>
                                    {searching ? <Spinner size='sm' /> : <div>
                                        <img src={search} style={{ marginRight: '6px' }} alt="" />
                                        Search
                                    </div>}
                                </button>
                            </InputGroupAddon>
                        </InputGroup>
                        {error.postcode &&  <Label style={{ color: '#B94A48', fontSize: '10px' }}>Enter a valid Post Code</Label>}
                    </Col>
                    <Col md='12' >
                        <Label className='mt-1'>Address  <span style={{ color: '#B94A48' }}>*</span></Label>
                        <><Select
                            ref={addressRef}
                            theme={selectThemeColors}
                            className='react-select'
                            classNamePrefix='select'
                            name='address'
                            maxMenuHeight={100}
                            onChange={selected => {
                                setUserInput({ ...userInput, address: selected.value })
                                seterror({ ...error, address: false })
                            }}
                            options={ukaddress.map(x => { return { value: x, label: x } })}
                            value={{ value: userInput.address, label: userInput.address || "Select Address" }}
                            // isDisabled={!ukaddress.length}
                            isClearable={false}
                        /><Input
                                required
                                style={{
                                    opacity: 0,
                                    width: "100%",
                                    height: 0
                                    // position: "absolute"
                                }}
                                onFocus={e => addressRef.current.select.focus()}
                                value={userInput.address || ''}
                                onChange={e => ''}
                                placeholder="Select Address"
                            />
                            {error.address && <Label style={{ color: '#B94A48', fontSize: '10px' }}>Please select address</Label>}</>
                    </Col>
                    <Col md="12 text-center">
                        <Button.Ripple style={{ width: '55%', fontSize: '14px' }} className='mt-2' color='primary' type="submit" disabled={isLoading}>
                            {isLoading ? <Spinner size='sm' /> : 'Submit'}
                        </Button.Ripple>
                    </Col>
                </Form>
            </div>
        </main>
    )
}

export default EstateAdminReg